<template>
	<section class="news">
		<h1 class="news_title">Последние новости:</h1>
		<Loader text="Загрузка новостей..." v-if="isLoading" />
		<article class="post" v-else v-for="post in posts" :key="post.id">
			{{ post.title }}
		</article>
	</section>
</template>

<script>
import Loader from "@/components/Loader.vue";

export default {
	components: {
		Loader,
	},
	data() {
		return {
			currentPage: 1,
			isLoading: false,
			posts: [],
		};
	},
	methods: {
		loadNews(page) {
			this.isLoading = true;
			this.$http
				.get("/handler.php", {
					params: {
						page: page,
					},
				})
				.then((response) => {
					this.currentPage = response.data.currentPage;
					this.posts = response.data.posts;
					this.isLoading = false;
				})
				.catch((error) => {
					console.log(error);
				});
		},
		changePage(page) {
			if (this.currentPage != page) {
				this.currentPage = page;
				this.loadNews(this.currentPage);
			}
		},
	},
	mounted() {
		if (this.$route.params.page !== undefined)
			this.currentPage = this.$route.params.page;

		this.loadNews(this.currentPage);
	},
};
</script>
